import axios from '@/utils/axios';

export interface ImgJson {
  diffuse: string
  flat: string
  height: string
  normal: string
  specular: string
  thumbnail: string
  toeThumbnail: string
  texture?: string

  glossiness: string
  // 有的有有的没有，条件暂时不明
  images?: string[]
  models?: string[]
  videos?: string[]
}

export interface MaterialGoods {
  id: string
  materialId: string
  materialName: string
  imgJson: ImgJson
  /* 点击数 */
  click?: number
  color?: string
  colorFamily?: string
  companyId?: string
  samplePrice?: number
  totalSize?: number
  [propName: string]: any

  materialType: number
  /** 供应商材料名称 */
  supplierArticleName: string
  supplierArticleNo: string
  supplierName: string
  /** 使用部位 */
  partUsed: string
  /** 起订量 */
  moq: number
  moqUnit: number
  season: string
  /** 样品周期 */
  samplePeriod: string
  samplePeriodUnit: number
  /** 大货周期；接口如此 */
  productionLeadTime: null
  productionLeadTimeUnit: string
  /* 规格 */
  specification: string
  /** 接口如此 */
  thickness:null
  /* 原产地 */
  materialCountryOfOrigin: string
}

/**
 * 查询面料详细信息
 */
export const getMaterialDetail = (id: string) => {
  return axios({
    method: 'get',
    url: '/mall/material',
    params: {
      id,
    },
  });
};

export const countSettledAxios = {
  props: {
    count: Number,
  },
  model: {
    prop: 'count',
    event: 'update',
  },
  methods: {
    addCount() {
      this.$emit('update', this.count + 1);
    },
  },
};
